<style>
  svg {
    height: 100%;
    width: 100%;
  }
  path {
    --color: var(--offwhite);
    --hoverColor: var(--charcoal);
    fill: rgb(var(--color));
  }

/*   svg:hover path {
    fill: rgb(var(--hoverColor));
  } */

  g{
    transform-origin:center;
    transform-box: fill-box;
  }
  svg g{
    animation: rotate 60s linear infinite;
  }
  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
</style>
